import React, {ReactElement} from 'react';
import styles from './not-found.module.css';
import {Button} from "@ya.praktikum/react-developer-burger-ui-components";
import {useNavigate} from "react-router-dom";
import image from "../images/not-found.png";
import {IClassNames} from "../utils/types";

export default function NotFound404(): ReactElement {
  const classNames: IClassNames = {
    container: styles.container,
    header: "text text_type_main-large pb-5",
    text: "text text_type_main-medium text_color_inactive pb-5",
    img: styles.img + " mb-10 mt-5",
    button: "mt-10"
  }

  const navigate = useNavigate();

  const onClick = (): void => {
    navigate('/');
  };

  return (
    <div className={classNames.container}>
      <h1 className={classNames.header}>!  404</h1>
      <img src={image} className={classNames.img} alt=""></img>
      <p className={classNames.text}>,     </p>
      <p className={classNames.text}>,   ,   :(</p>
      <Button
        htmlType="button"
        type="primary"
        size="medium"
        onClick={onClick}
        extraClass={classNames.button}
      >
          
      </Button>
    </div>
  );
}
